import { Injectable, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { accessTokenKey, baseUrl, storageKey } from './constants';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  constructor(
    @Inject(baseUrl) private _baseUrl: string,
    private _httpClient: HttpClient
  ) {}

  public logout() {
    localStorage.setItem(accessTokenKey, null);
  }

  public tryToLogin(options: { username: string; password: string }) {
    return this._httpClient.post<any>(`${this._baseUrl}api/users/token`, options).pipe(
      map(response => {
        localStorage.setItem(accessTokenKey, response.accessToken);
        return response.accessToken;
      })
    );
  }
}